import { uniSwiperDot } from "@dcloudio/uni-ui";
export default {
    data() {
        return {
            productList: [
                {
                    id: 1,
                    img: '',
                    name: '玻璃水',
                    price: '100.00'
                },
                {
                    id: 2,
                    img: '',
                    name: '洗车液',
                    price: '100.00'
                },
                {
                    id: 3,
                    img: '',
                    name: '蜡洗洗车液',
                    price: '100.00'
                },
                {
                    id: 4,
                    img: '',
                    name: '镀膜洗车液',
                    price: '100.00'
                }
            ],
            info: [{
                content: '内容 A'
            }, {
                content: '内容 B'
            }, {
                content: '内容 C'
            }],
            current: 0,
            mode: 'long',
            dotsStyles: {
                backgroundColor: "rgba(253, 208, 0, 0.3)",
                border: "1px rgba(253, 208, 0, 0.3) solid",
                color: "#fff",
                selectedBackgroundColor: "rgba(253, 208, 0, 0.7)",
                selectedBorder: "1px rgba(253, 208, 0, 0.7) solid"
            }
        }
    },
    methods: {
        change(e) {
            this.current = e.detail.current;
        },
        goProductDetail() {
            uni.navigateTo({
                url: "/pagesC/product_car_duster/product_car_duster"
            });
        }
    },
    components: {uniSwiperDot}
}
